@import "../../global.scss";

.works {
  background-color: crimson;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  .arrow {
    height: 50px;
    position: absolute;
    cursor: pointer;

    @include mobile {
      display: none;
    }

    &.left {
      left: 100px;
      transform: rotateY(180deg);
    }

    &.right {
      right: 100px;
    }
  }

  .slider {
    height: 350px;
    display: flex;
    position: absolute;
    left: 0;
    transition: all 1s ease-out;

    @include mobile {
      height:calc(100vh - 70px);
      flex-direction: column;
      
    }

    .container {
      width: 100vw;
      display: flex;
      align-items: center;
      justify-content: center;

      .item {
        width: 700px;
        height: 100%;
        background-color: white;
        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: center;

        @include mobile {
          width: 80%;
          height: 150px;
          margin: 15px 0;
        }

        .left {
          flex: 6;
          height: 80%;
          display: flex;
          align-items: center;
          justify-content: center;

          .leftContainer {
            width: 90%;
            height: 70%;
            display: flex;
            flex-direction: column;
           

            h1 {
              font-size: 30px;

              @include mobile{
                padding-top: 25px;
                font-size: 13px;
              }
            }

            h3 {
              font-size: 18px;
              color:crimson;

              @include mobile{
                font-size: 10px;
              }
            }

            p {
              padding-top: 10px;
              font-size: 13px;
              @include mobile{
                display: none;
              }
            }
           
          }
        }
        .right {
          flex: 6;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          overflow: hidden;

          img {
            width: max-content;
            height: max-content;
            transform: rotate(-5deg);
          }
        }
      }
    }
  }
}