@import "../../global.scss";

.intro {
  background-color: rgb(14, 13, 13);
  display: flex;
  

  @include mobile{
    flex-direction: column;
    align-items: center;
  }

  .left {
    flex: 0.5;
    overflow: hidden;

    .imgContainer {
      width: 700px;
      height: 700px;
      background-color: crimson;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      float: right;
      transform: rotate(-15deg);

      

      @include mobile{
        align-items: flex-start;
      }

      img {
        height: 75%;
        transition: all linear 3s;
        &:hover{
          transform: rotateY(3600deg);
        }

        @include mobile{
          height: 40%;
        }
      }
    }
  }
  .right {
    flex: 0.5;
    position: relative;
    overflow: hidden;

    .wrapper {
      width: 100%;
      height: 100%;
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      color: white;
      padding-left: 150px;
      padding-top: 230px;

      @include mobile{
        padding-left: 0;
        padding-top: 30px;
        align-items: center;
      }

      h1 {
        font-size: 60px;
        margin: 10px 0;

        @include mobile{
          font-size: 40px;
        }
      }
      h2 {
        font-size: 35px;
        @include mobile{
          font-size: 20px;
        }
      }
      h3 {
        font-size: 30px;

        @include mobile{
          font-size: 20px;
        }

        span {
          font-size: inherit;
          color: crimson;
        }

        .ityped-cursor {
          animation: blink 1s infinite;
        }

        @keyframes blink {
            50%{
                opacity: 1;
            }
            100%{
                opacity: 0;
            }
        }
      }
    }
    a {
      position: absolute;
      bottom: 10px;
      left: 35%;

      .down {
        font-size: 50px;
        color: white;
        animation: arrowBlink 2s infinite;
      }

      @keyframes arrowBlink {
        100% {
          opacity: 0;
        }
      }
    }
  }
}